import Vue from 'vue'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import { validationMixin, Vuelidate } from 'vuelidate'
import { required, email, minLength } from 'vuelidate/lib/validators'

Vue.use(Vuelidate)

export default {
  head() {
    return {
      title: 'Log-in ',
      meta: [{ hid: 'Log-in ', name: 'Log-in ', content: 'Log-in' }]
    }
  },
  data: () => ({
    alert: 'd-none',
    error_msg: '',
    loading: false,
    email: null,
    password: '',
    form: {
      email: '',
      password: ''
    },

    show1: false,
    rules: {
      required: value => !!value || 'Required.',
      min: v => v.length >= 6 || 'Min 6 characters'
    }
  }),
  loading: {
    color: 'blue',
    height: '20px'
  },

  mixins: [validationMixin],
  validations: {
    email: { required, email },
    password: { required, minLength: minLength(6) }
  },
  mounted() {
    this.$nextTick(() => {
      this.$nuxt.$loading.start()
      setTimeout(() => this.$nuxt.$loading.finish(), 2000)
    })
  },

  computed: {
    emailErrors() {
      const errors = []
      if (!this.$v.email.$dirty) return errors
      !this.$v.email.email && errors.push('Must be valid e-mail')
      !this.$v.email.required && errors.push('E-mail is required')
      return errors
    },
    passwordErrors() {
      const passerrors = []
      if (!this.$v.password.$dirty) return passerrors
      !this.$v.email.required && passerrors.push('Password is required')
      return passerrors
    }
  },
  middleware: ['guest'],
  // auth: false,
  //  auth: 'guest',
  methods: {
    loginwithfacebook() {
      this.$auth.loginWith('facebook')
    },
    loginwithgoogle() {
      // this.$auth.loginWith('google')
      this.$auth
        .loginWith('google', { params: { prompt: 'select_account' } })
        .then(() => {
          console.log('Logged in!')
        })
    },
    loginwithgithub() {
      // this.$auth.loginWith('google')

      this.$auth.loginWith('github')
    },
    clear() {
      this.form.email = ''
      this.form.password = ''
      this.email = ''
      this.password = ''
      this.select = null
      this.alert = 'd-none'
      this.error_msg = ''
      this.$v.$reset()
    },
    async login() {
      this.$v.email.$touch()
      this.$v.password.$touch()
      if (!this.$v.$invalid) {
        this.alert = 'd-none'
        this.loading = true
        // NProgress.configure({ parent: '#container' });
        NProgress.start()
        NProgress.configure({ showSpinner: false })
        this.form.email = this.email
        this.form.password = this.password
        await this.$axios.$get('/sanctum/csrf-cookie')
        try {
          this.$auth
            .loginWith('laravelSanctum', {
              data: {
                email: this.form.email,
                password: this.form.password
              }
            })
            .then(response => {
              console.log('Response is' + response)
              console.log(response)
              console.dir(response)
              this.loading = false

              NProgress.done()
            })
            .catch(error => {
              console.log(error)
              this.loading = false

              NProgress.done()
              console.log('err onRejected')

              this.alert = ''
              this.error_msg = error.response.data
            })
        } catch (error) {
          this.alert = ''
          this.error_msg = error.response.data
          console.log(error.response.data)
          if (error.response.status === 422) console.log(error.response.data)
        }
      }
    }
  }
}
